import React, {  useState, useContext } from 'react';
import products from './Products';
import './Items.css'
import {Link, Route} from 'react-router-dom';
import ViewCart from './Carts'


const Items =() =>{
    const PAGE_CART = 'cart';
    const PAGE_PRODUCT = 'products';
    const PAGE_HOME = 'home';


        
        const [cart, setCart] = useState([]);
        const [page,setPage] = useState(PAGE_PRODUCT);
        
        
        function ViewCart(){
            return(
                <div style={{alignItems:"center"}}>
                <button style={{alignSelf:"center"}}
                onClick={()=>{
                    setPage(PAGE_PRODUCT);
                }}>Go Back</button>
                {cart.map((item,idx)=>{
                    return(

                        <table class="table">
                            <tbody>
                                <tr className="trow">
                                <td className="tdata"><img style={{height:"100px", width:"100px"}} src={item.img}></img></td>
                                <td className="tdata">{item.product}</td>
                                <td className="tdata">{item.price}</td>
                                <td><button
                                    onClick={()=>{
                                        setCart(cart.filter(el => el !== item))
                                    }}>Remove</button></td>
                                </tr>
                            </tbody>
                        </table>
                        // <table style={{width:"100%"}}>
                        //     <tbody>
                        //         <tr>
                        //             <td><img style={{height:"100px", width:"100px"}} src={item.img} /></td>
                        //             <td>{item.product}</td>
                        //             <td>{item.price}</td>
                        //             <td><button
                        //                     onClick={()=>{
                        //                         setCart(cart.filter(el => el !== item))
                        //                     }}>Remove Item</button></td>
                        //         </tr>
                        //     </tbody>
                            
                        // </table>
                    )
                })}
                </div>
            )
            
            
        }
      
    
        
    return(
        <>
        {page === PAGE_PRODUCT ?
            (<>
                <header className="bg-dark py-5">
                <div className="container px-4 px-lg-5 my-5">
                    <div className="text-center text-white">
                        <h1 className="display-4 fw-bolder">Shop in style</h1>
                        <p className="lead fw-normal text-white-50 mb-0">With this shop hompeage template</p>
                    </div>
                </div>
            </header>
            
                <button className="bi-cart-fill me-1" href="/cart"
                onClick={()=>{
                    setPage(PAGE_CART);
                }}>Cart</button>
                                
                            
            
           
            <section className="py-5">
            <div className="container px-4 px-lg-5 mt-5">
                <div className="row gx-4 gx-lg-5 row-cols-2 row-cols-md-3 row-cols-xl-4 justify-content-center">

                    {products.map((el, idx)=>{
                        
                        return(
                            <>
                                <div className="col mb-5" key={el.id}>

                                        <div className="card h-100">
                                            {idx}
                                            {el.product === "Sale Item" ? (
                                                <>
                                                    <div className="badge bg-dark text-white position-absolute" style={{top: "0.5rem", right: "0.5rem"}}>Sale</div>
                                                </>
                                            ):("")}
                                            {el.product === "Special Item" ? (
                                                <>
                                                    <div className="badge bg-dark text-white position-absolute" style={{top: "0.5rem", right: "0.5rem"}}>Sale</div>
                                                </>
                                            ):("")}
                                            
                                            <img className="card-img-top" src={el.img} alt="..." />
                                        
                                            <div className="card-body p-4">
                                                <div className="text-center">
                                                    
                                                    <h5 className="fw-bolder">{el.product}</h5>
                                                    {el.product === "Special Item"  
                                                        ?(<div>
                                                                <div className="d-flex justify-content-center small text-warning mb-2">
                                                                    <i className="fas fa-star"></i>
                                                                    <i className="fas fa-star"></i>
                                                                    <i className="fas fa-star"></i>
                                                                    <i className="fas fa-star"></i>
                                                                    <i className="fas fa-star"></i>
                                                                    
                                                                </div>
                                                            </div>
                                                        )
                                                        :
                                                        ("") }
                                                        {el.product === "Popular Item"  
                                                        ?(<div>
                                                                <div className="d-flex justify-content-center small text-warning mb-2">
                                                                    <i className="fas fa-star"></i>
                                                                    <i className="fas fa-star"></i>
                                                                    <i className="fas fa-star"></i>
                                                                    <i className="fas fa-star"></i>
                                                                    <i className="fas fa-star"></i>
                                                                    
                                                                </div>
                                                            </div>
                                                        )
                                                        :
                                                        ("") }
                                                    
                                                       <span className="text-muted text-decoration-line-through">{el.initial}</span>  {el.price}
                                                </div>
                                            </div>
                                            
                                            <div className="card-footer p-4 pt-0 border-top-0 bg-transparent">
                                                <div className="text-center">
                                                    <button className="btn btn-outline-dark mt-auto" 
                                                    onClick={()=>{
                                                        setCart([...cart,{...el}]);
                                                        console.log(cart);
                                                        cart.map((item)=>{
                                                            return(
                                                                <>
                                                                    <div>{item.product}</div>
                                                                    <div>{item.price}</div>
                                                                </>
                                                            )
                                                        })  
                                                    }}
                                                    >{el.cart}</button></div>
                                            </div>
                                        </div>
                                </div>
                            </>
                        )
                    })}
                    
                    
                    </div>
                </div>
                    
            </section>
            </>
            ):(<ViewCart />)}
        
        </>
    )
}
export default Items;

